<template>
  <base-component>
    <Breadcrumb :style="{margin: '15px 0 15px 185px', position: 'fixed'}">
      <BreadcrumbItem to="/home"></BreadcrumbItem>
      <BreadcrumbItem to="/v2/mhaDbReplications">DB </BreadcrumbItem>
    </Breadcrumb>
    <Content class="content" :style="{padding: '10px', background: '#fff', margin: '50px 0 1px 185px', zIndex: '1'}">
      <div style="padding: 1px 1px ">
        <Row :gutter=10 align="middle">
          <Col span="10">
            <Card :padding=5>
              <template #title>DB</template>
              <Row :gutter=10 align="middle">
                <Col span="12">
                  <Input prefix="ios-search" v-model="relatedMhaDb.dbName" placeholder="DB "
                         @on-enter="getReplications(1)">
                  </Input>
                </Col>
                <Col span="12">
                  <Select filterable prefix="ios-home" clearable v-model="relatedMhaDb.buCode" placeholder=""
                          @on-change="getReplications(1)">
                    <Option v-for="item in buCodes" :value="item" :key="item">{{ item }}</Option>
                  </Select>
                </Col>
              </Row>
            </Card>
          </Col>
          <Col span="8">
            <Row :gutter=10 align="middle" v-show="preciseSearchMode">
              <Col span="12">
                <Card :padding=5>
                  <template #title></template>
                  <Row :gutter=10>
                    <Col span="24">
                      <Select filterable prefix="ios-pin" clearable v-model="srcMhaDb.regionId" placeholder=""
                              @on-change="getReplications(1)">
                        <Option v-for="item in regions" :value="item.id" :key="item.regionName">
                          {{ item.regionName }}
                        </Option>
                      </Select>
                    </Col>
                  </Row>
                </Card>
              </Col>
              <Col span="12">
                <Card :padding=5>
                  <template #title></template>
                  <Row :gutter=10>
                    <Col span="24">
                      <Select filterable prefix="ios-pin" clearable v-model="dstMhaDb.regionId"
                              placeholder=""
                              @on-change="getReplications(1)">
                        <Option v-for="item in regions" :value="item.id" :key="item.regionName">{{
                            item.regionName
                          }}
                        </Option>
                      </Select>
                    </Col>
                  </Row>
                </Card>
              </Col>
            </Row>
            <Row :gutter=10 align="middle" v-show="!preciseSearchMode">
              <Col span="24">
                <Card :padding=5>
                  <template #title></template>
                  <Row :gutter=10>
                    <Col span="24">
                      <Select filterable prefix="ios-pin" clearable v-model="relatedMhaDb.regionId"
                              placeholder=""
                              @on-change="getReplications(1)">
                        <Option v-for="item in regions" :value="item.id" :key="item.regionName">{{
                            item.regionName
                          }}
                        </Option>
                      </Select>
                    </Col>
                  </Row>
                </Card>
              </Col>
            </Row>
          </Col>
          <Col span="3">
            <Card :padding=5>
              <template #title></template>
              <Select filterable prefix="ios-pin" clearable v-model="drcStatus"
                      placeholder=""
                      @on-change="getReplications(1)">
                <Option v-for="item in drcStatusList" :value="item.value" :key="item.status">{{
                    item.status
                  }}
                </Option>
              </Select>
            </Card>
          </Col>
          <Col span="3">
            <Row :gutter=10 align="middle">
              <Button type="primary" icon="ios-search" :loading="dataLoading" @click="getReplications(1)"></Button>
              <i-switch v-model="preciseSearchMode" size="large" style="margin-left: 10px">
                <template #open>
                  <span></span>
                </template>
                <template #close>
                  <span></span>
                </template>
              </i-switch>
            </Row>
            <Row :gutter=10 align="middle" style="margin-top: 20px">
              <Button icon="md-refresh" @click="resetParam" :loading="dataLoading"></Button>
            </Row>
          </Col>
        </Row>
        <br>
        <Row  style="background: #fdfdff; border: 1px solid #e8eaec;">
          <Col span="2" style="display: flex;float: left;margin: 5px" >
            <Dropdown placement="bottom-start">
              <Button type="default" icon="ios-hammer">
                
                <Icon type="ios-arrow-down"></Icon>
              </Button>
                <template #list>
                  <DropdownMenu >
                    <DropdownItem @click.native="() => {$router.push({path: '/drcV2'})}">DRCMHA</DropdownItem>
                    <DropdownItem @click.native="() => {$router.push({path: '/v2/dbDrcBuild'})}">DRCDB</DropdownItem>
                  </DropdownMenu>
                </template>
              </Dropdown>
          </Col>
        </Row>
        <Table :loading="dataLoading" stripe border :columns="columns" :data="replications" :span-method="handleSpan">
          <template slot-scope="{ row, index }" slot="action">
            <Button type="primary" size="small" style="margin-right: 5px" @click="goToLink(row, index)">
              
            </Button>
          </template>
        </Table>
        <div style="text-align: center;margin: 16px 0">
          <Page
            :transfer="true"
            :total="total"
            :current.sync="current"
            :page-size-opts="[10,20,50,100]"
            :page-size="10"
            show-total
            show-sizer
            show-elevator
            @on-change="getReplications(current)"
            @on-page-size-change="handleChangeSize"></Page>
        </div>
        <Drawer title="Basic Drawer" width="80" :closable="true" v-model="replicationDetail.show">
          <template #header>
            
            <div style="float:right;margin-right: 100px">
              
              <i-switch v-model="replicationDetail.lineWrap"/>
              
              <i-switch v-model="replicationDetail.darkMode"
                        on-change="(status)=>{this.$Message.info(''+status)}"/>
            </div>
          </template>
          <div id="xmlCode">
            <codemirror
              v-model="replicationDetail.data"
              class="code"
              :options="{
                  mode: 'xml',
                  theme: replicationDetail.darkMode? 'monokai':'default',
                  autofocus: true,
                  lineWrapping: replicationDetail.lineWrap,
                  readOnly: true,
                  lineNumbers: true,
                  foldGutter: true,
                  styleActiveLine: true,
                  gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter']
            }">
            </codemirror>
            <Divider />
            <Button v-if="replicationDetail.deleteMode" type="error"  style="float:right;margin-right:20px" @click="deleteConfig(replicationDetail.replicationId)">
              
            </Button>
          </div>
        </Drawer>
      </div>
    </Content>
  </base-component>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import MhaGraph from '@/views/v2/meta/mhaReplicationDetails.vue'
import 'codemirror/theme/monokai.css'
import 'codemirror/mode/xml/xml.js'

import 'codemirror/addon/fold/foldgutter.css'
import 'codemirror/addon/fold/foldgutter.js'
import prettyMilliseconds from 'pretty-ms'

export default {
  name: 'Application',
  data () {
    return {
      columns: [
        {
          title: '',
          width: 80,
          render: (h, params) => {
            const row = params.row
            let text = 'none'
            let type = 'error'
            let disabled = false
            switch (row.transmissionType) {
              case 'simplex':
                text = ''
                type = 'info'
                break
              case 'duplex':
                text = ''
                type = 'success'
                break
              default:
                text = ''
                disabled = true
                break
            }
            return h('Button', {
              props: {
                type: type,
                size: 'small',
                disabled: disabled
              },
              on: {
                click: () => {
                  this.showModal(row)
                }
              }
            }, text)
          }
        },
        {
          title: '',
          key: 'drcStatus',
          width: 100,
          align: 'center',
          renderHeader: (h, params) => {
            return h('span', [
              h('span', ''),
              h('Button', {
                on: {
                  click: async () => {
                    await this.getDelay()
                  }
                },
                props: {
                  loading: this.delayDataLoading,
                  size: 'small',
                  shape: 'circle',
                  type: 'default',
                  icon: 'md-refresh'
                }
              })
            ])
          },
          render: (h, params) => {
            const row = params.row
            let color, text
            if (row.drcStatus === true) {
              if (row.delay != null) {
                console.log(row.delay)
                text = prettyMilliseconds(row.delay, { compact: true })
                if (row.delay > 10000) {
                  color = 'warning'
                } else {
                  color = 'success'
                }
              } else {
                text = ''
                color = 'blue'
              }
            } else {
              text = ''
              color = 'default'
            }
            return h('Tag', {
              props: {
                color: color
              }
            }, text)
          }
        },
        {
          title: 'DB ',
          key: 'srcDbName',
          render: (h, params) => {
            return h('p', params.row.src.dbName)
          }
        },
        {
          title: '',
          key: 'buText',
          width: 150,
          render: (h, params) => {
            return h('span', params.row.src.buName)
          }
        },
        {
          title: '',
          key: 'replicationText',
          render: (h, params) => {
            const row = params.row
            const color = 'blue'
            const text = `${row.src.regionName} -> ${row.dst.regionName}`
            return h('Tag', {
              props: {
                color: color
              }
            }, text)
          }
        },
        {
          title: '',
          key: 'replicationText',
          render: (h, params) => {
            const row = params.row
            const color = 'blue'
            const text = `${row.src.mhaName} -> ${row.dst.mhaName}`
            return h('Tag', {
              props: {
                color: color
              }
            }, text)
          }
        },
        {
          title: '',
          slot: 'action',
          width: 100,
          align: 'center'
        }
      ],
      // page
      total: 0,
      current: 1,
      size: 10,
      // query param
      srcMhaDb: {
        dbName: this.$route.query.srcDbName,
        buCode: null,
        regionId: null
      },
      dstMhaDb: {
        dbName: this.$route.query.dstDbName,
        buCode: null,
        regionId: null
      },
      relatedMhaDb: {
        dbName: this.$route.query.relatedDbName,
        buCode: null,
        regionId: null
      },
      drcStatus: this.$route.query.drcStatus ? Number(this.$route.query.drcStatus) : 1,
      preciseSearchMode: this.$route.query.preciseSearchMode === true || this.$route.query.preciseSearchMode === 'true',
      timerId: null,
      // get from backend
      replications: [],
      buCodes: [],
      regions: [],
      drcStatusList: [
        {
          status: '',
          value: 0
        },
        {
          status: '',
          value: 1
        }
      ],
      // for detail show
      replicationDetail: {
        show: false,
        replicationId: null,
        deleteMode: false,
        data: null,
        darkMode: true,
        lineWrap: false,
        row: {}
      },
      dataLoading: true,
      delayDataLoading: true
    }
  },
  computed: {},
  methods: {
    getBus () {
      this.axios.get('/api/drc/v2/meta/dbBuCodes/all')
        .then(response => {
          this.buCodes = response.data.data
        })
    },
    getRegions () {
      this.axios.get('/api/drc/v2/meta/regions/all')
        .then(response => {
          this.regions = response.data.data
        })
    },
    flattenObj (ob) {
      const result = {}
      for (const i in ob) {
        if ((typeof ob[i]) === 'object' && !Array.isArray(ob[i])) {
          const temp = this.flattenObj(ob[i])
          for (const j in temp) {
            result[i + '.' + j] = temp[j]
          }
        } else {
          result[i] = ob[i]
        }
      }
      return result
    },
    resetParam () {
      this.srcMha = {
        name: null,
        buCode: null,
        regionId: null
      }
      this.dstMha = {
        name: null,
        buCode: null,
        regionId: null
      }
      this.relatedMhaDb = {
        name: null,
        buCode: null,
        regionId: null
      }
      this.drcStatus = null
      this.getReplications(1)
    },
    async getReplications (pageIndex = 1) {
      this.resetPath()
      const that = this
      const params = {
        pageIndex: pageIndex,
        pageSize: this.size
      }
      params.relatedMhaDb = {
        dbName: this.relatedMhaDb.dbName,
        buCode: this.relatedMhaDb.buCode
      }
      if (this.preciseSearchMode) {
        params.srcMhaDb = {
          regionId: this.srcMhaDb.regionId
        }
        params.dstMhaDb = {
          regionId: this.dstMhaDb.regionId
        }
      } else {
        params.relatedMhaDb.regionId = this.relatedMhaDb.regionId
      }
      params.drcStatus = this.drcStatus
      const reqParam = this.flattenObj(params)
      that.dataLoading = true
      await that.axios.get('/api/drc/v2/replication/db/query', { params: reqParam })
        .then(response => {
          const pageResult = response.data.data
          if (!pageResult || pageResult.totalCount === 0) {
            that.total = 0
            that.current = 1
            that.replications = []
            that.$Message.warning('')
          } else {
            that.total = pageResult.totalCount
            that.current = pageResult.pageIndex
            that.calTableSpan(pageResult.data)
            pageResult.data.forEach(line => {
              line.delay = null
            })
            that.replications = pageResult.data
            that.$Message.success('')
            this.getDelay()
          }
        })
        .catch(message => {
          that.$Message.error(': ' + message)
        })
        .finally(() => {
          that.dataLoading = false
        })
    },
    getDelay () {
      const param = {
        replicationIds: this.replications
          .filter(item => item.drcStatus === true)
          .map(item => item.id)
          .join(',')
      }
      if (param.replicationIds.length === 0) {
        console.log('skip delay search')
        return
      }
      this.delayDataLoading = true
      this.axios.get('/api/drc/v2/replication/db/delay', { params: param })
        .then(response => {
          const delays = response.data.data
          const emptyResult = delays == null || !Array.isArray(delays) || delays.length === 0
          if (emptyResult) {
            return
          }
          const dataMap = new Map(delays.map(e => [e.srcMha + '->' + e.dstMha + '->' + e.dbName, e.delay]))
          this.replications.forEach(line => {
            line.delay = dataMap.get(line.src.mhaName + '->' + line.dst.mhaName + '->' + line.src.dbName)
          })
          console.log(this.replications)
        })
        .catch(message => {
          console.log(message)
          this.$Message.error(': ' + message)
        })
        .finally(() => {
          this.delayDataLoading = false
        })
    },
    handleChangeSize (val) {
      this.size = val
      this.$nextTick(() => {
        this.getReplications(1)
      })
    },
    handleBeforeChange () {
      console.log('handleBeforeChange:', this.switchOneInfo)
      return new Promise((resolve) => {
        this.$Modal.confirm({
          title: '',
          content: '',
          onOk: () => {
            resolve()
          }
        })
      })
    },
    switchMonitor (mhaName, status) {
      // 
      const switchStatus = status === 0 ? 'on' : 'off'
      this.doSwitchMonitor(mhaName, switchStatus)
    },
    doSwitchMonitor (mhaName, status) {
      console.log(mhaName)
      this.axios.post('/api/drc/v2/monitor/switch/' + mhaName + '/' + status).then(res => {
        if (res.data.status === 0) {
          console.log(status)
          if (status === 'on') {
            this.$Message.success('')
          } else {
            this.$Message.success('')
          }
        } else {
          this.$Message.warning('')
        }
        this.getReplications(1)
      })
    },
    deleteConfig (replictionId) {
      this.dataLoading = true
      this.axios.delete('/api/drc/v2/replication/offline?mhaReplicationId=' + replictionId).then(response => {
        if (response.data.status === 0) {
          this.$Message.success('')
        } else {
          this.$Message.warning(': ' + response.data.message)
        }
        this.getReplications(1)
      }).catch(message => {
        this.$Message.error(': ' + message)
      }).finally(() => {
        this.dataLoading = false
      })
    },
    checkConfig (replicationId, isDeleteMode) {
      this.dataLoading = true
      this.replicationDetail.data = null
      this.axios.get('/api/drc/v2/meta/queryConfig/mhaReplication', {
        params: {
          replicationId: replicationId
        }
      }).then(response => {
        if (response.data.status === 1) {
          this.$Message.warning(': ' + response.data.message)
          return
        }
        this.replicationDetail.data = response.data.data
        this.replicationDetail.replicationId = replicationId
        this.replicationDetail.deleteMode = isDeleteMode
        this.replicationDetail.show = true
      }).catch(message => {
        this.$Message.error(': ' + message)
      }).finally(() => {
        this.dataLoading = false
      })
    },
    goToLink (row, index) {
      this.$router.push({
        path: '/drcV2',
        query: {
          step: 3,
          srcMhaName: row.src.mhaName,
          dstMhaName: row.dst.mhaName
        }
      })
    },
    goToDrcLink () {
      this.$router.push({
        path: '/drcV2'
      })
    },
    showModal (row) {
      console.log('show modal')
      this.$Message.info('')
    },
    calTableSpan (replications) {
      console.log('calTableSpan', replications)
      if (replications == null || replications.length === 0) {
        return
      }
      const length = replications.length

      calSrcMhaSpan()
      calDstMhaSpan()

      function calSrcMhaSpan () {
        let i = 0
        while (i < length) {
          let j = i + 1
          while (j < length && replications[i].src.dbName === replications[j].src.dbName) {
            replications[j].src.rowSpan = 0
            replications[j].src.colSpan = 0
            j++
          }
          replications[i].src.rowSpan = j - i
          replications[i].src.colSpan = 1
          i = j
        }
      }

      function calDstMhaSpan () {
        let i = 0
        while (i < length) {
          let j = i + 1
          while (j < length && replications[i].dst.dbName === replications[j].dst.dbName) {
            replications[j].dst.rowSpan = 0
            replications[j].dst.colSpan = 0
            j++
          }
          replications[i].dst.rowSpan = j - i
          replications[i].dst.colSpan = 1
          i = j
        }
      }
    },
    handleSpan ({ row, column, rowIndex, columnIndex }) {
      if (column.key === 'srcDbName') {
        return [row.src.rowSpan, row.src.colSpan]
      } else if (column.key === 'dstDbName') {
        return [row.dst.rowSpan, row.dst.colSpan]
      }
    },
    goToHickwallMonitorPage (srcName, dstName) {
      const url = 'http://hickwall.ctripcorp.com/grafanav2/d/vWeGmjFVk/drc-mha?from=now-1h&to=now&var-mha=' + srcName +
        '&var-auto_gen_other_mha=' + dstName
      window.open(url, '_blank')
    },
    resetPath () {
      if (this.preciseSearchMode) {
        this.$router.replace({
          query: {
            srcDbName: this.srcMhaDb.dbName,
            dstDbName: this.dstMhaDb.dbName,
            drcStatus: this.drcStatus,
            preciseSearchMode: true
          }
          // eslint-disable-next-line handle-callback-err
        }).catch(err => {})
      } else {
        this.$router.replace({
          query: {
            relatedDbName: this.relatedMhaDb.dbName,
            drcStatus: this.drcStatus,
            preciseSearchMode: false
          }
          // eslint-disable-next-line handle-callback-err
        }).catch(err => {})
      }
      console.log('this.$route.query.preciseSearchMode', this.$route.query.preciseSearchMode, typeof this.$route.query.preciseSearchMode)
    }
  },
  created () {
    this.getReplications(1)
    this.getRegions()
    this.getBus()
    this.timerId = setInterval(() => this.getDelay(), 5000)
    setTimeout(() => { clearInterval(this.timerId) }, 30 * 60 * 1000)
  },
  beforeDestroy () {
    if (this.timerId) {
      clearInterval(this.timerId)
      this.timerId = null
    }
  }
}
</script>

<style scoped>

</style>
<style lang="scss">
#xmlCode {
  .CodeMirror {
    overscroll-y: scroll !important;
    height: auto !important;
  }
}
</style>
